@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap");

@font-face {
  font-family: "Montserrat";
  src: url("./assets/fonts/Montserrat-Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url("./assets/fonts/Montserrat-SemiBold.ttf");
  font-weight: 600;
}

@font-face {
  font-family: "Montserrat";
  src: url("./assets/fonts/Montserrat-Bold.ttf");
  font-weight: 700;
}

@font-face {
  font-family: "Montserrat";
  src: url("./assets/fonts/Montserrat-ExtraBold.ttf");
  font-weight: 800;
}

@font-face {
  font-family: "NotoSansCJKkr";
  src: url("./assets/fonts/NotoSansCJKkr-Light.otf");
  font-weight: 300;
}

@font-face {
  font-family: "NotoSansCJKkr";
  src: url("./assets/fonts/NotoSansCJKkr-DemiLight.otf");
  font-weight: 350;
}

@font-face {
  font-family: "NotoSansCJKkr";
  src: url("./assets/fonts/NotoSansCJKkr-Regular.otf");
  font-weight: 400;
}

@font-face {
  font-family: "NotoSansCJKkr";
  src: url("./assets/fonts/NotoSansCJKkr-Medium.otf");
  font-weight: 500;
}

@font-face {
  font-family: "NotoSansCJKkr";
  src: url("./assets/fonts/NotoSansCJKkr-Bold.otf");
  font-weight: 700;
}

@mixin TextStyle4 {
  font-family: "NotoSansCJKkr";
  font-weight: normal;
  font-size: 64px;
  line-height: 48px;
}

@mixin TextStyle5 {
  font-family: "Montserrat";
  font-weight: bold;
  font-size: 50px;
  line-height: 60px;
  text-align: center;
  letter-spacing: 5px;
}

@mixin TextStyle3 {
  font-family: "Montserrat";
  font-weight: bold;
  font-size: 45px;
  line-height: 60px;
  letter-spacing: 4.5px;
}

@mixin TextStyle2 {
  font-family: "NotoSansCJKkr";
  font-weight: 500;
  font-size: 35px;
  line-height: 30px;
}

@mixin TextStyle {
  font-family: "Montserrat";
  font-weight: 800;
  font-size: 20px;
  line-height: 25px;
}

$black: #1a1a1a;
$white: #ffffff;
$main-color: #ffffff;
$primary: #0565bb;
$yellow-txt: #fcd32a;
$white-txt: #ffffff;
$orange-txt: #f96400;
$black3-txt: #333333;
$black6-txt: #666666;
$goldenrod: #fccb05;
$bgr-brand-story: #0565bb;
$bgr-orange: #ff5500;

//spacing
@mixin btn-see-more {
  border: solid 3px $black;
  background-color: $white;
  width: 199px;
  height: 60px;
  font-family: Montserrat;
  font-size: 23px;
  font-weight: 800;
  line-height: 1.09;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $black;
  cursor: pointer;
}

@mixin border-box {
  border: solid 3px $black;
  background-color: $white;
}

@mixin flex-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

//same class
.img-bg {
  object-fit: contain;
  max-width: 100%;
  max-height: auto;
}

.txt-center {
  align-items: center;
}
